body {
  position: relative;
  font-family: "Kozuka Gothic Pro";
  color: #29292A;
  transition: 0.4s ease;
}
body.no-scroll {
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 100px;
}

::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 100px;
  cursor: pointer;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
  cursor: pointer;
}

a {
  transition: 0.4s ease;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

.btn {
  min-width: 200px;
  padding: 16px 40px;
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  border-radius: 100px;
}
@media only screen and (max-width: 1919.98px) {
  .btn {
    padding: 1.5625vw 3.125vw;
    font-size: 1.45833vw;
  }
}
@media only screen and (max-width: 991.98px) {
  .btn {
    font-size: 18px;
  }
}

.btn-primary {
  color: #ffffff;
  background-color: #a8ccdf;
  border-color: #a8ccdf;
}
.btn-primary:hover {
  color: #ffffff;
  background-color: #bde0f3;
  border-color: #bde0f3;
}
.btn-primary:focus, .btn-primary:active, .btn-primary:first-child:active {
  color: #ffffff;
  background-color: #a8ccdf;
  border-color: #a8ccdf;
}
.btn-primary.disabled {
  color: #ffffff;
}

.btn-secondary {
  background-color: #ffffff;
  border-color: #ffffff;
  color: #80a0b1;
  padding: 12px 40px;
}
.btn-secondary:hover {
  background-color: #F2F5F9;
  border-color: #F2F5F9;
  color: #80a0b1;
}
.btn-secondary:focus, .btn-secondary:active, .btn-secondary:first-child:active {
  background-color: #ffffff;
  border-color: #ffffff;
  color: #80a0b1;
}

.btn-standard {
  position: relative;
  display: inline-block;
  padding-left: 8px;
  padding-right: 8px;
  font-weight: 500;
  font-size: 20px;
  line-height: 40px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  color: #29292A;
}
.btn-standard:before {
  content: "";
  position: absolute;
  bottom: -20px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #29292A;
  transform-origin: bottom left;
  transition: transform 0.25s ease-out;
  transform: scaleX(1);
}
.btn-standard:after {
  content: "";
  position: absolute;
  bottom: -20px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #29292A;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
  transform: scaleX(0);
  transition-delay: 250ms;
}
.btn-standard:hover {
  color: #29292A;
}
.btn-standard:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
  transition-delay: 250ms;
}
.btn-standard:hover:before {
  transform: scaleX(0);
  transform-origin: bottom right;
}

.form-check .form-check-label {
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: #333333;
}
.form-check__count {
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: #A1A1A1;
}

