.form-control-custom,
.input-text {
  position: relative;
  width: 100%;
  padding: 10px 15px;
  border-radius: 0px;
  box-sizing: border-box;
  transition: 0.4s ease;
  border: 1px solid #DDDDDD;
  background-color: transparent;
  font-weight: 700;
  font-size: 30px;
  line-height: 42px;
  color: #232526;
  background-color: #ffffff;
}
.form-control-custom:focus,
.input-text:focus {
  border: 1px solid #a8ccdf;
  box-shadow: none;
  outline: none;
  background-color: #ffffff;
}
.form-control-custom::placeholder,
.input-text::placeholder {
  font-weight: 400;
  color: #68686b;
}
.form-control-custom.wpcf7-not-valid,
.input-text.wpcf7-not-valid {
  padding-right: 42px;
  background: url("https://api.iconify.design/ion/alert-circle.svg?color=%23cb3a31") no-repeat center center/contain;
  background-repeat: no-repeat;
  background-position: right 18px center;
  background-size: 24px 24px;
  border: 1px solid #CB3A31;
}
@media only screen and (max-width: 1919.98px) {
  .form-control-custom,
  .input-text {
    font-size: 1.5625vw;
    line-height: 1.7;
  }
