.scroll-down {
  position: absolute;
  left: 50%;
  bottom: 120px;
  transform: translateX(-50%);
  z-index: 99;
  color: #ffffff;
  text-align: center;
  width: 100%;
  cursor: pointer;
}
@media (max-width: 1024.98px) {
  .scroll-down {
    bottom: 50px;
  }
}
.scroll-down__desc {
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.scroll-down__icon {
  height: 25px;
  animation: move 2s linear infinite;
}
.scroll-down__icon path {
  fill: #ffffff;
}
@keyframes move {
  0% {
    transform: translateY(10px);
    opacity: 0;
  }
  50% {
    transform: translateY(40px);
    opacity: 1;
  }
  100% {
    transform: translateY(80px);
    opacity: 0;
  }
}

